// Importacion Estilos Globales
@import 'EstilosGlobales/EstilosGlobales.scss';

// Tama√±o min adaptable (activa responsive)
$widthResponsive: 700px;

// ---- ESTILOS COMPONENTE BARRA DE NAVEGACION ----
.contenedor-BarraDeNavegacion {
    height: 90px;

    border-bottom: 1px solid rgba(52, 52, 52, 0.399);
    box-shadow: -1px 21px 21px -15px rgba(0, 0, 0, 0.16);
    -webkit-box-shadow: -1px 21px 21px -15px rgba(0,0,0,0.28);
    -moz-box-shadow: -1px 21px 21px -15px rgba(0,0,0,0.28);
    position: absolute;
    width: 100%;
    background: rgba(255, 255, 255, 0.875);
    //left: 0;
    //right: 0;
    z-index: 10;

    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
}
.contenedor-logo-BarraDeNavegacion {
    padding: 3px;
    padding-left: 50px;
}

// Contenedor links 
.contenedor-link-BarraDeNavegacion{
    display: flex;
    flex-direction: row;

    @media (max-width:$widthResponsive){
        padding-top: 30px;        
        //padding: 10px 0px 20px 20px;
        background-color: rgba(248, 248, 248, 0.597);
        backdrop-filter: blur(10px);
        border-left: 2px  solid rgba(0, 0, 0, 0.484);
        border-bottom: 2px  solid rgba(0, 0, 0, 0.482);
        border-radius: 0px 0px 0px 10px;
        position: absolute;
        right: 0;
        top:91px;
        flex-direction: column;
        transition: 1s ease all ;

        &.menu-desactivo {
            height: 0px;
            padding: 0;
            border: 0;
            opacity: 0.4;

            .link-BarraDeNavegacion{
                visibility: hidden;
            }
            .boton-contacto-BarraDeNavegacion{
                visibility: hidden;
            }
        }
    }
}

// Estilos boton link del NAV
.link-BarraDeNavegacion {
    padding: 35px 40px 20px 40px;
    color: rgb(69, 69, 69);
    text-decoration: none;
    font-weight:bold;
    
    &:hover {
        color:  rgb(147, 0, 0);
        border-bottom: 5px solid rgba(150, 0, 0, 0.512);
    }
    &:active {
        color:  rgba(200, 0, 0, 0.749);
        border-bottom: 5px solid rgba(200, 0, 0, 0.748);
    }
    &.active{
        color:  rgb(175, 0, 0);
        border-bottom: 5px solid rgb(175, 0, 0); 
    }

    @media (max-width:$widthResponsive){
        text-align: center;
        width: 250px;
        padding: 20px;
        border-radius: 10px;
        border: 3px solid rgba(150, 0, 0, 0);  
        margin: 0px 25px 20px 25px; 

        &:hover {
            color:  rgb(147, 0, 0);
            border: 3px solid rgba(150, 0, 0, 0.512);  
        }
        &:active {
            color:  rgba(200, 0, 0, 0.749);
            border: 3px solid rgba(200, 0, 0, 0.748);
        }
        &.active{
            background: rgba(255, 0, 0, 0.073);
            color:  rgb(175, 0, 0);
            border: 3px solid rgb(175, 0, 0); 
        }
    }
}

// Boton de Contacto
.boton-contacto-BarraDeNavegacion{
    cursor: pointer;
    height: 90px;
    border: 0;
    background: rgba(71, 71, 71, 0.077);
    color: rgba(0, 0, 0, 0.492);
    padding: 20px;
    border-left: 1px solid rgba(0, 0, 0, 0.23);

    &:hover {
        color:  rgb(0, 0, 0);
    }
    &:active {
        color:  rgba(200, 0, 0, 0.815);
    }
    &.active{
        color:  rgb(0, 0, 0);
    }

    @media (max-width:$widthResponsive) {
        width: 100%;
        border-top: 1px solid rgba(0, 0, 0, 0.23);
        border-left: 0;
        border-radius: 0px 0px 0px 10px;
    }

}

// Contenedor Boton Desplegable
.contenedor-boton-desplegable-BarraDeNavegacion {
    display: none;
    @media (max-width:$widthResponsive) {
        display: flex;
        flex-flow: row nowrap;
    }
}
