// Importacion Estilos Globales
@import 'EstilosGlobales/EstilosGlobales.scss';


// ---- ESTILOS COMPONENTE SECCION DESCRIPCION ----
.contenedor-Descripcion {
    padding: 30px;
    background: rgba(255, 174, 0, 0.14);
    height: 100%;
}
.contenedor {
    background-image: url('../../../Imagenes/bg1.jpg');
    background-blend-mode:multiply;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 200px;
    width: 140px;
    height: 140px;
    position: relative;
    border: 3px solid rgb(86, 113, 158);
    
    img {
        transition: 1s ease all ;
        transform: translate3d(25px, 3px, 0px);

            position: absolute;
            width: 100px;
            bottom: 0;
        }
    &:hover {
        img {
            transform: scale3d(1.3, 1.3, 1.3) translate3d(18px, -15px, 0px);
            }
    }
}