// Importacion Estilos Globales
@import 'EstilosGlobales/EstilosGlobales.scss';




// ---- ESTILOS COMPONENTE SECCION PRESENTACION ----
.contenedor-Presentacion {
    padding: 30px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    height: 100%;
    min-height: 500px;
    padding-top: 150px;
    background-image: url('../../../Imagenes/imagenFondo2.avif'), linear-gradient(65deg, rgb(0, 0, 0) 0%, rgb(255, 255, 255) 19%, rgb(255, 255, 255) 30%, rgb(0, 0, 0) 100%);
    background-color: #00000066;
    background-blend-mode:multiply;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.seccion-texto-Presentacion {
    width: 500px;
    margin: 0;
    color: #dadada;
    text-rendering: optimizelegibility;
    font-weight: normal;
    font-family: "proxima nova bold", "Helvetica Neue", Helvetica, Arial, Sans-serif;
}

